export default {
	data() {
		return {
			tab: [{
					name: '平台动态',
					id: 0,
				},
				{
					name: '公司动态',
					id: 1,
				},
				{
					name: '个人动态',
					id: 2,
				}
			],
			tabid: 0,
			showPlatform:true,
			showOne: false,
			showCompany: false,
			nice: "../../static/nice.png",
			niceActive: "../../static/niceActive.png",
			companyList: [],
			talks: [],
			talksAnimationData: {},
			openid: '',
		}
	},
	onLoad() {
		// this.shareReq();
	},
	onShow() {
		// 评论弹出层动画创建
		this.animation = wx.createAnimation({
			duration: 400, // 整个动画过程花费的时间，单位为毫秒
			timingFunction: "ease", // 动画的类型
			delay: 0 // 动画延迟参数
		})
		this.shareReq()
	},
	methods: {
		changeTab(id) {
			this.tabid = id;
			// console.log(id);	
			if (this.tabid == 0) {
				this.showPlatform = true;
				this.showCompany = false;
				this.showOne = false;
			} else if (this.tabid == 1) {
				this.showCompany = true;
				this.showOne = false;
				this.showPlatform = false;
			}else if (this.tabid == 2) {
				this.showCompany = false;
				this.showOne = true;
				this.showPlatform = false;
			}
		},

		//请求数据
		async shareReq() {
			var res = await this.$request.get('/share');
			console.log(res);
			this.companyList = res.data;
		},

		// 动画加载数据
		loadTalks(uid) {
			this.animation.bottom("10rpx").height("100%").step({
				duration: 1000
			});

			this.reviewReq(uid);

			this.talksAnimationData = this.animation.export()
		},

		async reviewReq(uid) {
			var data = {
				'uid': uid
			};
			var res = await this.$request.get('/share?uid=' + uid);
			this.talks = res.data.review;
		},

		async changeNice(index, uid, openid) {
			var data = {
				'uid': uid,
				'flag': 1,
			};
			// var res = await this.$request.get('http://127.0.0.1:5000/share?uid=' + uid + 'flag=1');
			// console.log(res);
			// this.shareReq()
			var like = this.companyList.find(function(like) {
				return like.uid == uid
			})

			// console.log(like);
			var likearr = uni.getStorageSync('like') || [];
			// console.log(likearr);
				var result = likearr.some(item=>{
				if(item.uid==uid){
				return true
				}
				})
				if(result){
					//取消收藏
					var cancelarr = uni.getStorageSync('like');
					cancelarr.splice(index, 1);
					uni.setStorageSync('like', cancelarr);
					console.log(cancelarr);
					uni.showToast({
						icon: 'none',
						title: '取消收藏成功!'
					})
				}else {
					//收藏
					likearr.push(like);
					console.log(likearr);
					uni.setStorageSync('like', likearr);
					uni.showToast({
						icon: 'none',
						title: '收藏成功！'
					})
				}
				
		},

		toReview(uid, openid) {
			this.loadTalks(uid);
		},
		closeReview() {
			this.hideTalks()
		},
		hideTalks() {
			// 设置动画内容为：使用绝对定位隐藏整个区域，高度变为0
			this.animation.bottom("-100%").height("0rpx").step({
				duration: 1000
			});
			this.talks = [];
			this.talksAnimationData = this.animation.export()
		},

		//动态详情
		detail(uid, openid) {
			uni.navigateTo({
				url: '/pages/roomDetail/roomDetail?uid=' + uid + '&openid=' + openid,
			})
		}
	}
}
